/* harmony default export */ __webpack_exports__["default"] = ("<nav class=\"navbar text-white bg-info text-center\">\n  <h2>Social Presentation</h2>\n  <small>GenerazioneY</small>\n</nav>\n\n<br>\n\n<div class=\"container-fluid\">\n  <div class=\"row jumbotron\">\n\n    <div class=\"col-3 text-center\">\n      <i class=\"fa fa-facebook-square fa-lg sp-icon\" (click)=\"loadChartData('fb')\"></i>\n      <div class=\"under-icon\"></div>\n    </div>\n\n    <div class=\"col-3 text-center\">\n      <i class=\"fa fa-instagram fa-lg sp-icon\"       (click)=\"loadChartData('ig')\"></i>\n      <div class=\"under-icon\"></div>\n    </div>\n\n    <div class=\"col-3 text-center\">\n      <i class=\"fa fa-twitter-square fa-lg sp-icon\"  (click)=\"loadChartData('tw')\"></i>\n      <div class=\"under-icon\"></div>\n    </div>\n\n    <div class=\"col-3 text-center\">\n      <i class=\"fa fa-youtube-square fa-lg sp-icon\"  (click)=\"loadChartData('yt')\"></i>\n      <div class=\"under-icon\"></div>\n    </div>\n\n  </div>\n</div>\n\n<label class=\"container_checkmark pull-right\">\n  <input type=\"checkbox\" [(ngModel)]=\"isChecked\" (change)=\"onlyProv(isChecked)\">\n  <span class=\"checkmark\"></span>\n  &nbsp; <small>Mostra solo province</small>\n</label>\n<div style=\"clear: both;\"></div>\n\n<div id=\"chartdiv\"></div>\n\n<br>\n\n<div class=\"container\">\n\n    <button type=\"button\" class=\"btn btn-primary\" (click)=\"mappaCollapse = !mappaCollapse\" [attr.aria-expanded]=\"!mappaCollapse\" aria-controls=\"mappaDesc\">\n      Mappa & Valori\n    </button>\n    <hr>\n    <div id=\"mappaDesc\" [collapse]=\"mappaCollapse\" [isAnimated]=\"true\">\n      <p>Ogni circonferenza nella mappa rappresenta un comune, passando il cursore del mouse sopra la circonferenza è possibile visualizzare il numero di residenti del relativo comune.</p>\n      <p>La dimensione della circonferenza è definita dal numero di residenti, il colore della circonferenza è definito da una formula che considera il numero di followers e la frequenza dei post della pagina del social network selezionato.</p>\n      <p>Se la circonferenza è bianca, significa che il comune non è presente nel social network selezionato.</p>\n      <p>La formula considerata è la seguente:</p>\n\n      <div>\n        <ng-katex-paragraph [paragraph]=\"formula\"></ng-katex-paragraph>\n      </div>\n    </div>\n\n\n    <button type=\"button\" class=\"btn btn-primary\" (click)=\"noteCollapse = !noteCollapse\" [attr.aria-expanded]=\"!noteCollapse\" aria-controls=\"note\">\n      Note Metodologiche\n    </button>\n    <hr>\n    <div id=\"note\" [collapse]=\"noteCollapse\" [isAnimated]=\"true\">\n      <p><b>REPORT DI MONITORAGGIO CIVICO “LA COMUNICAZIONE SOCIAL DEI COMUNI SICILIANI”.</b></p>\n      <p><b>Data inizio monitoraggio:</b>30 luglio 2019</p>\n      <p><b>Data fine monitoraggio:</b>03 settembre 2019</p>\n      <p><b>Enti monitorati:</b>390 Comuni della Sicilia.</p>\n      <p><b>Fonte di riferimento utilizzata per effettuare l’attività di monitoraggio civico:</b>Vademecum “<i>Pubblica Amministrazione e social media</i>”, a cura di Formez PA (scaricabile al seguente link: <a href=\"http://www.formez.it/notizie/online-il-vademecum-pubblica-amministrazione-e-social-media.html\">http://www.formez.it/notizie/online-il-vademecum-pubblica-amministrazione-e-social-media.html</a>), realizzato nell’ambito delle attività finalizzate all’elaborazione delle Linee guida per i siti web delle Pubbliche Amministrazioni, previste dalla Direttiva n. 8 del 26 novembre 2009 del Ministro per la pubblica amministrazione e l’innovazione, dicembre 2011.</p>\n      <p><b>NB:</b>I dati utilizzati per realizzare il presente Report sono pubblicamente disponibili, visualizzabili e reperibili all’interno dei principali social network monitorati nell’ambito del presente Report.</p>\n      <p>I Parametri di monitoraggio utilizzati sono i seguenti:</p>\n      \n      <p>\n        Link Pagina/Profilo/Account Social Network:\n        <br>Data di creazione:\n        <br>Numero Like Pagina/Profilo: \n        <br>Followers:\n        <br>Following:\n        <br>Iscritti:\n        <br>Post:\n        <br>Frequenza di pubblicazione:\n        <br>Persone che gestiscono la pagina:\n        <br>Numero di post con hashtag #nomecomune\n        <br>Numero Video:\n        <br>Numero di Views totali:\n      </p>\n\n      <p>I dati riportati possono essere suscettibili di variazioni e modificazioni dalla data di completamento dal monitoraggio alla data di pubblicazione dei relativi risultati successiva alle fasi di analisi, rilevazione e comparazione effettuate dal team di attivisti volontari coinvolti nel progetto.</p>\n      <p><b>PS:</b> Il presente Report è realizzato come progetto di “<i>Civic Activism</i>”, diretto a monitorare il flusso comunicativo dei Comuni visibile sui social network, secondo il tipico modello partecipativo di OpenGov, per fornire uno spunto di riflessione soggettiva - senza nessuna pretesa di verità assoluta - sull’impatto sociale delle nuove tecnologie e contribuire, con spirito costruttivo, alla diffusione della cultura digitale.</p>\n      <p>Il presente Caso di Studio è finalizzato a favorire la partecipazione interattiva dal basso degli utenti/cittadini, nonché a stimolare processi di governance aperti e trasparenti, con l’obiettivo di creare un sistema di reciproca fiducia sull’operato e sulle scelte delle amministrazioni e contribuire al progressivo incremento della qualità della trasparenza delle PA, attraverso proposte, suggerimenti e iniziative, con funzioni propositive di stimolo e di impulso, nell’ambito di una rete collaborativa paritaria costituita da enti pubblici, organizzazioni no-profit e cittadini, in grado di realizzare un innovativo modello orizzontale e partecipativo, in cui il processo decisionale costituisce il risultato del dialogo e della collaborazione tra istituzioni e cittadini.</p>\n    </div>\n\n    <button type=\"button\" class=\"btn btn-primary\" (click)=\"statsCollapse = !statsCollapse\" [attr.aria-expanded]=\"!statsCollapse\" aria-controls=\"stats\">\n      Dati statistici riassuntivi\n    </button>\n    <hr>\n    <div id=\"stats\" [collapse]=\"statsCollapse\" [isAnimated]=\"true\">\n        <br><b>Provincia: Agrigento (AG)</b>\n        <br>Comuni monitorati: 43\n        <br>Pagine Facebook attive: 25\n        <br>Profili Twitter attivi: 3\n        <br>Profili Instagram attivi: 3\n        <br>Canali YouTube attivi: 11\n        <br>Profili LinkedIn attivi: 0\n        <br>\n        <br>\n        <br><b>Provincia: Caltanissetta (CL)</b>\n        <br>Comuni monitorati: 22\n        <br>Pagine Facebook attive: 19\n        <br>Profili Twitter attivi: 1\n        <br>Profili Instagram attivi: 2\n        <br>Canali YouTube attivi: 9\n        <br>Profili LinkedIn attivi: 0\n        <br>\n        <br>\n        <br><b>Provincia: Catania (CT)</b>\n        <br>Comuni monitorati: 58\n        <br>Profili Facebook attivi: 52\n        <br>Profili Twitter attivi: 10\n        <br>Profili YouTube attivi: 27\n        <br>Profili Instagram attivi: 13\n        <br>Profili LinkedIn attivi: 3\n        <br>\n        <br>\n        <br><b>Provincia: ENNA (EN)</b>\n        <br>Comuni monitorati: 20\n        <br>Pagine Facebook attive: 14\n        <br>Profili Twitter attivi: 4\n        <br>Profili Instagram attivi: 1\n        <br>Canali YouTube attivi: 7\n        <br>Profili LinkedIn attivi: 1\n        <br>\n        <br>\n        <br><b>Provincia: Messina (ME)</b>\n        <br>Comuni monitorati: 108\n        <br>Pagine Facebook attive: 80\n        <br>Profili Twitter attivi: 14\n        <br>Profili Instagram attivi: 8\n        <br>Canali YouTube attivi: 27\n        <br>Profili LinkedIn attivi: 1\n        <br>\n        <br>\n        <br>\n        <br><b>Provincia: PALERMO (PA)</b>\n        <br>Comuni monitorati: 82\n        <br>Pagine Facebook attive: 67\n        <br>Profili Twitter attivi: 11\n        <br>Profili Instagram attivi: 16\n        <br>Canali YouTube attivi: 23\n        <br>Profili LinkedIn attivi: 3\n        <br>\n        <br>\n        <br><b>Provincia: Ragusa (RG)</b>\n        <br>Comuni monitorati: 12\n        <br>Profili Facebook attivi: 11\n        <br>Profili Twitter attivi: 5\n        <br>Profili YouTube attivi: 5\n        <br>Profili Instagram attivi: 3\n        <br>Profili LinkedIn attivi: 1\n        <br>\n        <br>\n        <br><b>Provincia: Siracusa (SR)</b>\n        <br>Comuni monitorati: 21\n        <br>Pagine Facebook attive: 19\n        <br>Profili Twitter attivi: 6\n        <br>Profili Instagram attivi: 7\n        <br>Canali YouTube attivi: 10\n        <br>Profili LinkedIn attivi: 1\n        <br>\n        <br>\n        <br><b>Provincia Trapani (TP)</b>\n        <br>Comuni monitorati: 24\n        <br>Pagine Facebook attive: 18\n        <br>Profili Twitter attivi: 5\n        <br>Profili Instagram attivi: 2\n        <br>Canali YouTube attivi: 12\n        <br>Profili LinkedIn attivi: 0\n    </div>\n\n</div>\n\n<br>\n<br>\n<br>\n<br>\n<br>\n\n<footer>\n  <nav class=\"navbar bg-info text-center text-white\">\n    <span class=\"text-center\" style=\"width: 100%\">\n      Coded By <a href=\"https://github.com/Helias\" class=\"text-white\">Stefano Borzì</a> & <a href=\"https://github.com/daxcpp\" class=\"text-white\">Davide Carnemolla</a>\n    </span>\n  </nav>\n</footer>\n");

